		transform: translateY(4px);
		width: 30px
	}

	.angle-right {
		transform: rotate(-45deg);
		-webkit-transform: rotate(-45deg)
	}

	.angle-left {
		transform: rotate(135deg);
		-webkit-transform: rotate(135deg)
	}

	.angle-up {
		transform: rotate(-135deg);
		-webkit-transform: rotate(-135deg)
	}

	.angle-down {
		transform: rotate(45deg);
		-webkit-transform: rotate(45deg)
