	.gh-arrow:before {
		background-color: inherit;
		content: "";
		height: 2px;
		position: absolute;
		width: 22px
	}

	.gh-arrow:after {
		right: 15px;
		top: 7px;
		transform: rotate(45deg)
	}

	.gh-arrow:before {
		right: 15px;
		top: -7px;
		transform: rotate(-45deg)
	}

